import React, { useEffect, useState } from "react";
import { load_products } from "../redux/actions/products";
import { useDispatch, useSelector } from "react-redux";
import { selectProducts, selectCategories } from "../redux/selectors/products";

import { useParams } from "react-router-dom";
import { HomeCategoriesData } from "../components/HomeProducts/HomeCategoriesData";
import { useTranslation } from "react-i18next";
import BigHeader from "../components/Products/BigHeader";
import Breadcumb from "../components/Products/Breadcumb";
import ProductsMap from "../components/Products/ProductsMap";

// First we take redux store data
const mapState = (state) => ({
	products: selectProducts(state),
	prev: state.products.prev,
	next: state.products.next,
	count: state.products.count,
	categories: selectCategories(state),
});

function FilterProducts() {
	const [loadMore, setLoadMore] = useState(null);
	const [foundFilter, setFoundFilter] = useState(null);
	const [isLoading, setIsLoading] = useState(false);
	const [clickedLoadMore, setClickedLoadMore] = useState(false);
	const { t, i18n } = useTranslation(["home", "common"]);
	// Take url param
	const { url } = useParams();
	// Take redux info
	const { products, count } = useSelector(mapState);
	const dispatch = useDispatch();

	useEffect(() => {
		window.scrollTo({
			top: 0,
			behavior: "smooth",
		});
		setFoundFilter(HomeCategoriesData.find((data) => data.name === url));
	}, [url]);

	useEffect(() => {
		if (!foundFilter) {
			setFoundFilter(HomeCategoriesData.find((data) => data.name === url));
		} else {
			const confi = `${foundFilter?.api_link}&${
				loadMore ? `&limit=20&offset=${loadMore}` : ""
			}`;
			setClickedLoadMore(false);
			setIsLoading(true);
			if (loadMore) {
				dispatch(load_products(confi, products, i18n.language)).then((res) => {
					setIsLoading(false);
				});
			} else {
				dispatch(load_products(confi, null, i18n.language)).then((res) => {
					setIsLoading(false);
				});
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [foundFilter, clickedLoadMore, i18n.language]);

	return (
		<>
			<BigHeader
				searchedCategoryName={
					isLoading ? (
						<div class="spinner-border text-white" role="status">
							<span class="visually-hidden">Loading...</span>
						</div>
					) : (
						t(`categories.${foundFilter?.name}`)
					)
				}
			/>

			<div className="container">
				<Breadcumb
					topmar={false}
					searchedCategoryName={t(`categories.${foundFilter?.name}`)}
				/>

				{/* Products Count Start */}
				<div className="row align-items-center justify-content-between mb-1 mt-5">
					<div className="col-12  py-2">
						<h5>
							{count} {t("common:items_found")}
						</h5>
					</div>
				</div>
				<hr style={{ color: "#6270FB" }} />
				{/* Products Count END */}

				<ProductsMap
					setClickedLoadMore={setClickedLoadMore}
					setLoadMore={setLoadMore}
					isLoading={isLoading}
				/>
			</div>
		</>
	);
}

export default FilterProducts;
